
body, h2, p{
  margin:0;
  padding: 0;
}

body{
  background-color: blueviolet;
  padding: 3em;
}


header img{
  width: 10%;
  padding-bottom: 30px;
}

main{
  display: flex;
  justify-content:left
}

main img{
  width: 50%;
  padding-top: 30px;
  padding-right: 10%;
}
.content{
  
  padding-top: 4em;
}
.content h2{
  width: 35%;
  font-size: 30px;
  color: white;
  padding-bottom: -2em;
}

.content p{
  width: 60%;
  margin: 2em 0em;
  font-size: 20px;
  color: white;
}

main button{
  padding: 2em;
  border: none;
  border-radius: 30px;
  color: purple;
  background-color: white;
  width: 40%;
  padding: 1.5em;
}
footer {
  display: flex;
  grid-column: 1/4;
  justify-content: flex-end;
  padding-top: 100px;
}
footer .fab {
  color: white;
  font-size: 20px;
  border: 1px solid white;
  padding: .5em;
  border-radius: 50%;
  margin-right: 1em;
}
footer .fab:hover {
  border-color: purple;
  color: purple;
}
